.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  animation: fadeInUp 1s cubic-bezier(0.23, 1, 0.32, 1);
}

.App {
  text-align: center;
}

header {
  background-color: #282c34;
  height: auto;
  padding: 10px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 2px);
  color: white;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: coral;
  animation: fadeInBg 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

@keyframes fadeInBg {
  from {
    opacity: 0;
    filter: blur(8px);
  }
  to {
    opacity: 1;
    filter: blur(0);
  }
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.nav-menu {
  list-style: none;
  display: flex;
  justify-content: center;
  flex: 1;
  padding: 0;
  margin: 0;
}

.nav-menu li {
  margin: 0 15px;
  display: inline-block;
  opacity: 0;
  animation: navItemFadeIn 0.7s forwards;
}

.nav-menu li:nth-child(1) { animation-delay: 0.1s; }
.nav-menu li:nth-child(2) { animation-delay: 0.2s; }
.nav-menu li:nth-child(3) { animation-delay: 0.3s; }
.nav-menu li:nth-child(4) { animation-delay: 0.4s; }
.nav-menu li:nth-child(5) { animation-delay: 0.5s; }

.nav-menu a {
  text-decoration: none;
  color: #fff;
  padding: 10px 15px;
  border-radius: 10px;
  display: inline-block;
  transition: background-color 0.3s;
}

.nav-menu a:hover {
  background-color: black;
}

.auth-btn {
  padding: 10px 25px;
  background-color: coral;
  color: black;
  border: 2px solid #ffff;
  border-radius: 20px;
  cursor: pointer;
}

.auth-btn a {
  text-decoration: none;
  color: black;
}

.auth-btn:hover {
  background-color: #198fcf;
  color: #fff;
  border-color: #198fcf;
}


.get-started-btn {
  padding: 10px 25px;
  background-color: coral;
  color: black;
  border: 2px solid #ffff;
  border-radius: 20px;
  cursor: pointer;
}

.get-started-btn a {
  text-decoration: none;
  color: black;
}

.get-started-btn:hover {
  background-color: #198fcf;
  color: #fff;
  border-color: #198fcf;
}

main {
  flex: 1;
}

.auth-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  max-width: 400px;
  margin: 50px auto;
  box-sizing: border-box;
  width: 100%;
  position: relative;
  left: 0;
  top: 0;
  z-index: 10;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 80vh;
  padding: 20px;
  box-sizing: border-box;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
  box-shadow: black 0px 0px 20px 0px;
  backdrop-filter: blur(200px);
}

.auth-form input {
  width: 300px;
  padding: 10px;
  margin: 10px 0;
  border: 1.6px solid black;
  border-radius: 20px;
}

.auth-form button {
  padding: 10px 25px;
  background-color: #ffff;
  color: black;
  border: 2px solid #ff8357;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s, color 0.3s, box-shadow 0.3s, transform 0.2s;
}

.auth-form button:hover {
  background-color: #ff8357;
  animation: buttonPop 0.3s;
  border-color: #fff;
}

.auth-form h2 {
  margin-bottom: 20px;
  color: #282c34;
}

.auth-form p {
  margin-top: 10px;
  color: #555;
}

.auth-form a {
  color: #0056b3;
  text-decoration: none;
}

.auth-form a:hover {
  text-decoration: underline;
}

footer {
  background-color: #282c34;
  color: white;
  padding: 10px;
  width: 100%;
  position: relative;
  left: 0;
  bottom: 0;
  text-align: center;
  z-index: 100;
  box-sizing: border-box;
  max-width: 100vw;
  overflow-x: hidden;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

footer p {
  margin: 0;
}

@keyframes buttonBounce {
  0% {
    transform: scale(1);
  }
  30% {
    transform: scale(1.12);
  }
  60% {
    transform: scale(0.96);
  }
  100% {
    transform: scale(1);
  }
}

.get-started-btn, .auth-form button {
  transition: background 0.3s, color 0.3s, box-shadow 0.3s, transform 0.2s;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.get-started-btn:hover, .auth-form button:hover {
  animation: buttonBounce 0.4s;
  background: #198fcf;
  color: #fff;
  border-color: black;
}


.auth-btn {
  transition: background 0.3s, color 0.3s, box-shadow 0.3s, transform 0.2s;
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.auth-btn:hover {
  animation: buttonBounce 0.4s;
  background: #198fcf;
  color: #fff;
  border-color: black;
}


@keyframes navItemFadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.route-fade-in {
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

home-container, .about-container, .contact-container, .services-container, .blog-container, main {
  animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

